/*********************
*** Navbar section
**********************/

.header-nav {
    position:fixed;
    height:55px;
    width:100%;
    top:0;
    left:0;
    right:0;
    padding:10px 10px 0;
    z-index:1000;
    pointer-events:none;
}

.brand {
    position:absolute;
    top:0;
    left:0;
    pointer-events:auto;
    font-size:16px;
    padding: 23px 36px;
    opacity:1;

    -webkit-transition: opacity 0.3s;

    -o-transition: opacity 0.3s;

    transition: opacity 0.3s;
}

.brand a {
    text-decoration: none;
    color:#1a1a1a;
    font-family: "Roboto", sans-serif;
    font-weight:700;
    letter-spacing:0.05em;
}

.brand.hidden {
    opacity:0;
}

.toggle {
    position:absolute;
    right:0;
    top:0;
    cursor:pointer;
    pointer-events:auto;
}

.toggle span {
    position:absolute;
    right:36px;
    top:27px;
    letter-spacing: 3.2px;
    font-weight:700;
}

.toggle .menu-text {
    font-size:14px;
}

.light .toggle .menu-text {
    color:#fff;
}

/*********************
*** Canvas section
**********************/

#header-canvas-wrap {
    position:absolute;
    left:0;
    top:0;
    height:100%;
    width:100%;
}

/*********************
*** Full Menu section
**********************/

.header-wrap .full-menu {
    position:fixed;
    top:-120%;
    left:0;
    height:100vh;
    width:100%;

    background-color: rgba(122, 31, 61, 0.9);
    z-index:999;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction:column;
        flex-direction:column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack:center;
        justify-content:center;

    -webkit-transition: top 0.5s cubic-bezier(0.55, 0.055, 0.675, 0.19);

    -o-transition: top 0.5s cubic-bezier(0.55, 0.055, 0.675, 0.19);

    transition: top 0.5s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.header-wrap.active .full-menu {
    top:0%;

    -webkit-transition: top 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);

    -o-transition: top 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);

    transition: top 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.header-wrap.active .toggle .menu-text {
    color:#fff;
}

.menu-link {
    /*position:absolute;*/
    display:block;
    font-size:48px;
    font-weight:900;
    color:#fff;
    text-decoration:none;
    margin-bottom:1em;
}

.menu-link.selected {
    color:#888;
    pointer-events:none;
    cursor:pointer;
}

.menu-link:hover {
    color: rgba(255,255,255,0.6);
}

