    top:25%;
    left:25%;
}

.menu-link.about {
    top:48%;
    right:25%;
}

.menu-link.contact {
    bottom:33%;
    left:35%;
}
*/

@media print {
    .header-wrap .full-menu {
        display:none;
    }

    .header-wrap .toggle {
        display:none;
    }
}


